@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 20px;
}

.group {
    display: flex;
    flex-direction: column;
    width: 300px;
    align-self: center;
}


label {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    text-align: right;
    font-size: medium;
    line-height: 26px;
    width: 400px;
    margin-bottom: 10px;
    color: gray;
    align-items: center;
}
  

.dateinput {
  height: 20px;
  flex: 0 0 200px;
  margin-left: 10px;
  padding-top: 9px;
  padding-bottom: 9px;
  padding-left: 5px;
  margin-top: 5px;
  font-size: medium;
  color: gray;
  margin-bottom: 5px;
  border-radius: 5px;
  border-width: 0.5px;
  border-color: #d6d6d6;
  border-style: solid;
  width: 250px;
}


.enter {
    background-color: #2762e0;
    border-radius: 15px;
    padding: 5px 40px;
    cursor: pointer;
    color: white;
    font-weight: bold;
    font-size: 1.3vw;
    
}

.button {
  align-self: center;
  margin-bottom: 10px;
}

.alert {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: rgb(243, 138, 138);
  margin-left: 40%;
  margin-right: 40%;
  min-width: 400px;
  margin-top: 10px;
  border-radius: 5px;
  padding: 2px;
}

.alertheading {
  flex: 1 1;
  font-weight: bold;
  color: rgb(65, 53, 53);
}

.alert > button {
  align-self: flex-end;
  border: none;
  background: none;
  cursor: pointer;
  font-weight: bold;
}

#disabled {
  cursor: not-allowed;
  background-color: gray;
}

.loader-container {
  display: flex;
  justify-content: center;
}

.currencies {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
  margin-right: 10px;
}

.title {
  font-size: 50px;
  flex: 1 1;
  font-family: 'Open-Sans', sans-serif;
  font-weight: bolder;
  margin: 20px;
  color: #2762e0;
  display: flex;
  justify-content: flex-start;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-grow: 0;
}

.border {
  border-style: solid;
  border-color: lightgray;
  border-radius: 10px;
  border-width: 2px;
  margin-right: 30px;
  margin-left: 30px;
  flex: 1 1;
  min-height: 100%;
}

.master {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  min-height: 95vh;
}


.airplane {
  object-fit: contain;
  max-width: 50%;
  max-height: 50%;
  align-self: center;
}






.result {
    background-color: aliceblue;
    padding: 10px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-left: 20%;
    margin-right: 20%;
    min-width: 600px;
    border-radius: 5px;
    flex-direction: row;
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
}

#cheapest {
    padding: 20px;
    background-color: rgb(175, 218, 255);
    font-size: larger;
    margin-left: 18%;
    margin-right: 18%;
    padding-top: 30px;
    padding-bottom: 30px;
}

.price {
    font-weight: bold;
    font-size: larger;
    align-self: center;
    flex-grow: 1;
    flex-basis: 80px;
}

.price-container{
    display: flex;
    flex-grow: 1;
    flex-basis: 80px;
    justify-content: center;
}

.carrier {
    color: rgb(97, 97, 97);
    order: 50;
    align-self: center;
    font-weight: bold;
    flex-grow: 1;
    flex-basis: 80px;
}

.locations {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    flex-basis: 80px;
    justify-content: space-around;
}

.airports {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.location {
    margin-left: 10px;
    margin-right: 10px;
    padding-right: 5px;
    padding-left: 5px;
}

#arrow {
    align-self: flex-start;
    margin-top: -3px;
}

.icons {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-left: 5px;
    margin-right: 5px;
}


.date {
    font-size: large;
    color: #2762e0;
    font-weight: bold;
    
}

.direct {
    font-size: small;
    margin-top: 15px;
    
}


.double {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    flex-grow: 1;
    flex-basis: 80px;
}

#roundtripDate {
    align-self: center;
}


.search {
    margin-bottom: 20px;
}
.error {
    background-color: rgb(255, 223, 129);
    padding: 10px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-top: 50px;
    margin-left: 30%;
    margin-right: 30%;
    min-width: 300px;
    border-radius: 5px;
    flex-direction: row;
    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
    color: rgb(136, 102, 0);
    font-weight: bold;
}
.button-currency {
    border-color: gray;
    border-radius: 5px;
    border-width: 2px;
    border-style: solid;
    padding: 10px;
    color: gray;
    padding-right: 15px;
    padding-left: 15px;
    cursor: pointer;
    align-self: center;
    flex: 1 1;
    display: flex;
    justify-content: flex-end;
}

.select {
    width: 200px;
    border-color: gray;
    border-radius: 5px;
    border-width: 2px;
    border-style: solid;
    padding: 10px;
    color: gray;
    padding-right: 15px;
    padding-left: 15px;
}
